* {
    box-sizing: border-box;
    &:focus {
        outline: none;
    }
}

html,
body {
    outline: 0;
}

body {
    position: relative;
    width: 100%;
    height: 100vh;
    font-family: $font-body;
    background-color: $c-background;
    font-size: $font-size;
}

a {
    text-decoration: none;
    color: inherit;
}